.news-card-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #f5f6f7;
  color: #1a1b22;
}

.news-card-list__wrapper {
  width: 100%;
  max-width: 1232px;
  padding: 0;
  margin: 62px auto;
  display: grid;
  grid-template-columns: repeat(3, minmax(224px, 1fr));
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  list-style: none;
  box-sizing: border-box;
  background-color: #f5f6f7;
  position: relative;
}

@media screen and (max-width: 1312px) {
  .news-card-list__wrapper {
    grid-column-gap: 8px;
    grid-row-gap: 8px;
    padding: 0 40px;
  }
}

@media screen and (max-width: 768px) {
  .news-card-list__wrapper {
    grid-template-columns: repeat(auto-fill, minmax(224px, 1fr));
    margin: 32px auto;
  }
}

@media screen and (max-width: 680px) {
  .news-card-list__wrapper {
    width: 90%;
    padding: 0;
    margin: 24px auto;
    grid-template-columns: 100%;
    grid-column-gap: 0;
    grid-row-gap: 10px;
  }
}